.Newsletter_container{
height: 60vh;
background-color:#fcf5f5;
display: flex;
align-items: center;
justify-content: center;
flex-direction:column ;
}
.Newsletter_tittle{
font-size:70px ;
margin-bottom: 20px; 
}
.Newsletter_desc{
font-size: 24px;
font-weight:300 ;
margin-bottom: 20px;
}
.Newsletter_inputcontainer{
width: 50%;
height: 40px;
background-color: white;
display: flex;
justify-content: space-between;
border:1px solid lightgray;

}
.Newsletter_input{
   border: none;
    flex:8;
    /* padding-left: 20px; */
    cursor:pointer ;
    width:97% ;
    height:97%;
}
.Newsletter_Btn{
    border: none;
    flex:.5;
    background-color:teal ;
    color:white;
    display: flex;
    align-items: center;
    justify-content: center;
    padding:10px;
}